.menu {
  background-color: #1e3264;
  color: #566da7;
  border-radius: 2px;
  width: 300px;
  height: 100%;
  position: relative;
  overflow: hidden;
}
.menu header .bar,
.menu footer .bar {
  height: 60px;
  position: relative;
}
.menu header img,
.menu footer img {
  height: 24px;
  width: 24px;
  position: absolute;
  top: 18px;
  left: 12px;
}
.menu header p,
.menu footer p {
  margin: 0;
  margin-left: 47px;
  padding-top: 13px;
  line-height: 1.24em;
}
.menu header {
  background-color: #199bd7;
  color: #ffffff;
  position: relative;
}
.menu header a {
  color: #ffffff;
}
.menu header ol {
  margin-bottom: 0;
  border-top: 1px solid #53b4e1;
  background-color: #199bd7;
  width: 100%;
  position: absolute;
  z-index: 1000;
}
.menu header ol a:hover {
  background-color: #53b4e1;
  color: #ffffff;
}
.menu header .bar {
  background-color: #199bd7;
  z-index: 2000;
}
.menu header #menu-header-submenu {
  width: 100%;
}
.menu header #menu-header-submenu.open {
  top: 60px;
}
.menu footer {
  background-color: #1e3264;
  color: #ffffff;
}
.menu footer a {
  color: #566da7;
}
.menu footer i {
  margin-left: 2px;
}
.menu footer #menu-footer-submenu {
  top: 0;
  width: 100%;
  height: 100%;
}
.menu footer #menu-footer-submenu ol {
  margin-top: 60px;
  margin-bottom: 0;
}
.menu footer #menu-footer-submenu.open ol {
  background-color: #1e3264;
}
.menu ol {
  padding-left: 0;
}
.menu li {
  display: block;
}
.menu li a {
  display: block;
  padding: 10px 15px;
  text-decoration: none;
}
.menu .body li a,
.menu footer li a {
  background-color: #1e3264;
  color: #566da7;
}
.menu .body li a i,
.menu footer li a i {
  color: #566da7;
}
.menu .body li a span,
.menu footer li a span {
  padding-left: 10px;
}
.menu .body li a:hover,
.menu footer li a:hover {
  background-color: #263c73;
  color: #ffffff;
}
.menu .body li a:hover i,
.menu footer li a:hover i {
  color: #566da7;
}
.menu .body li.active a,
.menu footer li.active a,
.menu .body li.active:hover a,
.menu footer li.active:hover a,
.menu .body li.active:focus a,
.menu footer li.active:focus a {
  background-color: #16264e;
  color: #ffffff;
}
.menu .body li.active a i,
.menu footer li.active a i,
.menu .body li.active:hover a i,
.menu footer li.active:hover a i,
.menu .body li.active:focus a i,
.menu footer li.active:focus a i {
  color: #199bd7;
}
.label-transparent {
  border: 1px solid #ffffff;
}
.menu-dropdown {
  position: absolute;
  top: -100%;
  -webkit-transition: top 0.3s ease-out;
  -o-transition: top 0.3s ease-out;
  transition: top 0.3s ease-out;
}
.menu-dropdown.open {
  top: 0;
}
.menu-dropright {
  position: absolute;
  left: 100%;
  -webkit-transition: left 0.3s ease-out;
  -o-transition: left 0.3s ease-out;
  transition: left 0.3s ease-out;
}
.menu-dropright.open {
  left: 0;
}
.flex-col {
  display: flex;
  flex-direction: column;
}
.flex-row {
  display: flex;
}
.flex-1 {
  flex: 1;
}
.flex-middle {
  margin-top: auto;
  margin-bottom: auto;
}
